import { useState } from 'react'
import { useSelector } from 'react-redux'
import { Link, NavLink } from 'react-router-dom'
import styled from 'styled-components'
import useCustomLogin from '../../hooks/useCustomLogin'

const HeaderContainer = styled.header`
  position: relative;

  > div {
    max-width: 1280px;
    margin: 0 auto;
    padding: 16px;
  }
`
const Container = styled.div`
  display: flex;
  position: relative;
`

const Logo = styled.h1`
  background-image: url('https://cdn-icons-png.flaticon.com/512/29/29302.png');
  background-repeat: no-repeat;
  background-size: contain;

  > a {
    display: block;
    height: 40px;
    font-size: 20px;
    font-weight: 600;
    line-height: 40px;
    text-indent: 50px;
  }
`
const Navigation = styled.nav`
  position: absolute;
  top: 20px;
  right: 16px;

  > .gnb {
    display: flex;
    gap: 0 24px;
    > li {
      position: relative;
      font-size: 16px;

      > a {
        display: block;
        height: 34px;
        color: #374151;
        transition: all 0.2s;

        &.on,
        &:hover {
          color: #2563eb;
        }
      }

      > .lnb {
        display: ${(props) => (props.$show ? 'block' : 'none')};
        position: absolute;
        top: 56px;
        left: 0;
        z-index: 1000;
        width: 160px;
        animation: fade 0.3s;

        a {
          display: inline-block;
          padding: 8px 0px;
          &:hover {
            color: #fff;
            background-color: #3b82f6;
          }
        }
      }
    }
  } //end of nav
`

const BgLnb = styled.div`
  display: ${(props) => (props.$show ? 'block' : 'none')};
  position: absolute;
  left: 0;
  top: 72px;
  z-index: 1000;
  width: 100%;
  height: 120px;
  background-color: #88b6ff;
  background-image: url(https://img.freepik.com/free-vector/light-grey-dots-background_78370-2583.jpg?semt=ais_hybrid&w=740);
  /* background-size: 96px; */
  border-top: 3px solid #3b82f6;
  border-bottom: 1px solid #bfdbfe;
  animation: fade 0.3s;
`

/**
 * Link, NavLink      History API        .
 * NavLink             CSS   .
 * <NavLink to="" className={({isActive}) => isActive ? 'on' : undefined}> </NavLink>
 * <NavLink to="" style={({isActive}) => isActive ? activeStyle : undefined}> </NavLink>
 *
 */

//prettier-ignore
const Header = () => {

  const [showLnb, setShowLnb] = useState(false)
  const { isLogin } = useCustomLogin()

  console.log('isLogin =',isLogin)
  return (
    <HeaderContainer>
      <div>      
        <BgLnb onMouseLeave={()=>setShowLnb(false)} $show={showLnb} />
        <Container>
          <Logo>
            <Link to={'/'}></Link>
          </Logo>

{/* 
    styled-components show  CSS   props , HTML    show="false"   .
  styled-components $  prop  DOM        .

  <NavLink>     ,  isActive     
  <NavLink> state	     (useLocation()   )

    <Link to="/bookdetail" state={{ bookId: 123, title: ' ' }} >

    const location = useLocation();
    const { bookId, title } = location.state || {}; // state   
*/}
          <Navigation onMouseEnter={()=>setShowLnb(true)}  $show={showLnb}>
            <ul className="gnb">
              <li>
                <NavLink to={'/'} className={({isActive}) => isActive ? 'on' : undefined}>Home</NavLink>
              </li>
              <li>
                <NavLink to={'/books'} className={({isActive}) => isActive ? 'on' : undefined}></NavLink>
              </li>
              <li>
                <NavLink to={'/rental'} className={({isActive}) => isActive ? 'on' : undefined}></NavLink>
                <ol className="lnb">
                  <li><Link to={'/rental/list'}></Link></li>
                  <li><Link to={'/rental/over'}></Link></li>
                </ol>
              </li>
              <li>
                <NavLink to={'/wish'} className={({isActive}) => isActive ? 'on' : undefined}></NavLink>
                <ol className="lnb">
                  <li><Link to={'/wish/list'}></Link></li>
                  <li><Link to={'/wish/apply'}></Link></li>
                </ol>
              </li>
              <li>
                <NavLink to={'/cs'} className={({isActive}) => isActive ? 'on' : undefined}></NavLink>
                <ol className="lnb">
                  <li><Link to={'/cs/qna'}></Link></li>
                  <li><Link to={'/cs/faq'}>FAQ</Link></li>
                  <li><Link to={'/cs/notice'}></Link></li>
                </ol>
              </li>
              <li>
                <NavLink to={'/mypage'} className={({isActive}) => isActive ? 'on' : undefined}></NavLink>
                <ol className="lnb">
                  <li><Link to={'/mypage/dash'}></Link></li>
                  <li><Link to={'/mypage/info'}></Link></li>
                  <li><Link to={'/mypage/withdraw'}></Link></li>
                </ol>
              </li>
              <li>
                {isLogin? 
                (<NavLink to={'/logout'} className={({isActive}) => isActive ? 'on' : undefined}></NavLink> ):
                (<NavLink to={'/login'} className={({isActive}) => isActive ? 'on' : undefined}></NavLink>) }
              </li> 
            </ul>
          </Navigation>
        </Container>
      </div>
    </HeaderContainer>
  )
}

export default Header
